ReactDOM.render(
  <HashRouter>
    <div>
      <Switch>
        <Route exact path="/">
          {/*<React.StrictMode>*/}
            <NavBar />
            <Title />
            <Gallery />
            <FootNote />
          {/*</React.StrictMode>*/}
        </Route>
        <Route path="/signuplogin">
          <NavBar />
          <SignupLogin />
        </Route>
        <Route path="/accountPage">
          {/*<NavBar />*/}
          <AccountPage />
        </Route>
        {/*<Route exact path="/signuplogin">
          <NavBar />
          <Signup />
          {/*<AccountPage /> /}
          <div id="canvas" />
        </Route>*/}
        <Route path="/instagram">
          <NavBar />
          <GalleryInstagramStyle />
        </Route>
      </Switch>
    </div>
  </HashRouter>,
  document.getElementById('root')
);

